import { PublicClientApplication } from '@azure/msal-browser';

const VFSAuth =  (function () {
  const MSAL_CONFIG = {
    auth: {
      clientId: '9ee43215-5d48-4863-bcea-4aab5f29ae1c',
      authority: 'https://login.microsoftonline.com/f25493ae-1c98-41d7-8a33-0be75f5fe603',
    },
  };

  const msalInstance = new PublicClientApplication(MSAL_CONFIG);
  const scopeRequestConfig = {
    scopes: ['user.read']
  };

  const getAccessToken = async () => {
    const allAccounts = msalInstance.getAllAccounts();
    try {
      if (allAccounts && allAccounts.length <= 0) throw new Error('login_required');
      const silentResult = await msalInstance.acquireTokenSilent({
        scopeRequestConfig,
        account: allAccounts[0]
      });

      return {
        token: silentResult.accessToken,
        idToken: silentResult.idToken,
        account: allAccounts[0]
      }
    } catch (err) {
      if (isInteractionNeeded(err)) {
        try {
          const interactiveResult = await msalInstance.acquireTokenPopup(scopeRequestConfig);
          const allAccounts = msalInstance.getAllAccounts();

          return {
            token: interactiveResult.accessToken,
            idToken: interactiveResult.idToken,
            account: allAccounts[0]
          }

        } catch (e) {
          return {
            token: null,
            idToken: null,
            account: null
          }
        }

      } else {
        throw err;
      }
    }
  };

  const isInteractionNeeded = (error) => {
    if (!error.message || error.message.length <= 0) {
      return false;
    }

    return (
      error.message.indexOf('consent_required') > -1 ||
      error.message.indexOf('interaction_required') > -1 ||
      error.message.indexOf('login_required') > -1 ||
      error.message.indexOf('no_account_in_silent_request') > -1
    );
  };

  const parseJwt = (token) => {
    const base64Url = token.split('.')[1];
    const base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
    const jsonPayload = decodeURIComponent(atob(base64).split('').map(function (c) {
      return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
    }).join(''));

    return JSON.parse(jsonPayload);
  };

  const getRolesFromParsedJwt = (parsedJWT) => {
    if (parsedJWT?.roles instanceof Array) {
      return parsedJWT?.roles[0]?.toUpperCase();
    } else{
      return parsedJWT?.roles?.toUpperCase();
    }
  };

  const getUserRole = (idToken) => {
    const parsedJWT = parseJwt(idToken);
    return getRolesFromParsedJwt(parsedJWT);
  }

  const getAllAccounts = () => {
    return msalInstance.getAllAccounts();
  }

  const signOut = async (homeAccountId, postLogoutRedirectUri) => {
    const currentAccount = msalInstance.getAccountByHomeId(homeAccountId);
    return await msalInstance.logoutRedirect({
      account: currentAccount,
      postLogoutRedirectUri
    })
  }

  const getUserFromIdToken = (idToken) => {
    return parseJwt(idToken)
  }

  return {
    azureProvider: msalInstance,
    signIn: getAccessToken,
    signOut,
    getUserRole,
    getAllAccounts,
    getUserFromIdToken
  };
})();

export default VFSAuth;