import React from 'react'
import PropTypes from 'prop-types'
import { App } from './components/App'
import { ContextWrapper } from './components/ContextWrapper'
import { DataToolIcon } from './assets/DataToolIcon';

const Component = ({ state }) => {
  return <ContextWrapper><App /></ContextWrapper>
}

const metadata = {
  title: 'Data Access Control',
  requiredPermissions: ['Admin', 'Super.Admin'],
  description: 'An app to determine user access levels',
  icon: DataToolIcon
}

export { Component as default, metadata }

App.propTypes = {
  state: PropTypes.shape({
    locale: PropTypes.string,
    changeLocale: PropTypes.func,
    idToken: PropTypes.string
  }),
};